import './style/grapes.scss';
import './style/main.scss';

export default (editor, opts = {}) => {
  const options = {
    ...{
      // default options
    }, ...opts
  };

  // before init
  jQuery(document).trigger('pagedesigner-before-init', [editor, options]);


  // load default trait definition
  jQuery(document).trigger('pagedesigner-init-default-trait', [editor, options]);
  // load external trait definitions
  jQuery(document).trigger('pagedesigner-init-traits', [editor, options]);


  // define base components
  jQuery(document).trigger('pagedesigner-init-base-components', [editor, options]);
  // load external component definitions
  jQuery(document).trigger('pagedesigner-init-components', [editor, options]);


  // define blocks
  jQuery(document).trigger('pagedesigner-init-base-blocks', [editor, options]);
  // load external block definitions
  jQuery(document).trigger('pagedesigner-init-blocks', [editor, options]);


  // define base commands
  jQuery(document).trigger('pagedesigner-init-base-commands', [editor, options]);
  // load external command definitions
  jQuery(document).trigger('pagedesigner-init-commands', [editor, options]);


  // Add base panels
  jQuery(document).trigger('pagedesigner-init-base-panels', [editor, options]);
  // load external panel definitions
  jQuery(document).trigger('pagedesigner-init-panels', [editor, options]);



  // init events
  jQuery(document).trigger('pagedesigner-init-events', [editor, options]);




};
